"use client";

import { useEffect, useState } from "react";
import HeartChart from "../components/ui/HeartChart";
import { Heart, Activity, AlertTriangle } from "lucide-react";

export default function Dashboard() {
  const [bpmData, setBpmData] = useState<{ time: string; bpm: number }[]>([]);
  const [currentBpm, setCurrentBpm] = useState<number>(0);

  // Simulated real-time updates
  useEffect(() => {
    const interval = setInterval(() => {
      const bpm = 60 + Math.floor(Math.random() * 40); // simulate 60â€“100 bpm
      setCurrentBpm(bpm);
      const now = new Date();
      setBpmData((prev) => {
        const newData = [...prev, { time: now.toLocaleTimeString(), bpm }];
        return newData.slice(-20); // keep last 20 points
      });
    }, 2000);
    return () => clearInterval(interval);
  }, []);

  return (
    <main className="min-h-screen bg-gray-50 p-6">
      <h1 className="text-2xl font-semibold mb-6">Heart Monitor Dashboard</h1>

      {/* Chart Section */}
      <div className="bg-white rounded-2xl shadow p-6 mb-6">
        <h2 className="text-lg font-medium mb-4">Live Heart Rate</h2>
        <HeartChart data={bpmData} />
      </div>

      {/* Vitals Section */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        <div className="bg-white p-4 rounded-xl shadow flex items-center gap-3">
          <Heart className="w-6 h-6 text-red-500" />
          <div>
            <p className="text-sm text-gray-500">Pulse</p>
            <p className="text-lg font-bold">{currentBpm} bpm</p>
          </div>
        </div>

        <div className="bg-white p-4 rounded-xl shadow flex items-center gap-3">
          <Activity className="w-6 h-6 text-green-500" />
          <div>
            <p className="text-sm text-gray-500">Status</p>
            <p className="text-lg font-bold">
              {currentBpm >= 60 && currentBpm <= 100 ? "Normal" : "Alert"}
            </p>
          </div>
        </div>

        <div className="bg-white p-4 rounded-xl shadow flex items-center gap-3">
          <AlertTriangle className="w-6 h-6 text-yellow-500" />
          <div>
            <p className="text-sm text-gray-500">Alerts</p>
            <p className="text-lg font-bold">
              {currentBpm < 60 || currentBpm > 100 ? "High Risk" : "None"}
            </p>
          </div>
        </div>
      </div>
    </main>
  );
}
